* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  display: block;
}

p {
  font-size: 0.7rem;
  opacity: 0.7;
  color: #314c6e;
}
h1 {
  color: #426696;
  opacity: 0.8;
  font-size: 2rem;
}
h2,
h4 {
  color: #658ec6;
}
h3 {
  color: #426696;
  font-weight: 600;
  opacity: 0.8;
}
h5 {
  color: #426696;
  font-weight: 600;
  opacity: 0.8;
}

.main {
  min-height: 100vh;
  background: linear-gradient(to right top, #4974a5e0, #5892ad);
  display: flex;
  align-items: center;
  justify-content: center;
}

.glass {
  font-family: "Poppins", sans-serif;
  min-height: 80vh;
  width: 60%;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  border-radius: 2rem;
  z-index: 2;
  backdrop-filter: blur(1rem);
  display: flex;
}

.circle1,
.circle2 {
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  height: 20rem;
  width: 20rem;
  position: absolute;
  border-radius: 50%;
}

.circle1 {
  top: 5%;
  right: 15%;
}
.circle2 {
  bottom: 5%;
  left: 10%;
}

.Bio {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  border-radius: 2rem;
}

.text,
.skills {
  width: 80%;
  display: block;
}

.avatar {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.avatar img {
  height: 6rem;
  width: 6rem;
  object-fit: cover;
  border-radius: 50%;
}

.avatar h3 {
  margin-top: 0.8rem;
}

.avatar p {
  font-size: .8rem;
}

.contact {
  height: auto;
  width: 80%;
  background: linear-gradient(to top, #4974a536, #5892adc0);
  border-radius: 1rem;
  color: white;
  padding: 1rem;
  overflow: hidden;
  box-shadow: 6px 6px 20px rgba(100, 89, 89, 0.418);
}

.contact h4 {
  padding-bottom: 0.2rem;
  color: rgba(255, 255, 255, 0.877);
}
.contact p {
  opacity: 0.95;
}
.skills {
  text-align: center;
}
.Projects {
  flex: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.Projects h1 {
  padding: 1rem 0 0;
  width: 80%;
}

.cardContainer {
  position: relative;
  width: 80%;
  height: 7rem;
  overflow: hidden;
  box-shadow: 6px 6px 20px rgba(129, 80, 80, 0.308);
  border-radius: 1rem;
}

.cardFront {
  top: 0;
  left: 0;
  width: 100%;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
  background: linear-gradient(
    to right bottom,
    rgb(255, 255, 255, 1),
    rgba(255, 255, 255, 0.8)
  );

  border-radius: 1rem;

  transition: left 500ms ease-out .15s;
}

.cardBack {
  top: 0px;
  left: 29.5rem;
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  background: linear-gradient(to right bottom, #343b42d8, #273035be);
  border-radius: 1rem;
  transition: left 500ms ease-out .15s;
  color: white;
  padding: 2rem;
}

.cardBack p {
  color: white;
  width: 90%;
}

.projectDescription {
  width: 60%;
}
.imgContainer {
  width: 20%;
  height: 100%;
}
.cardFront img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.cardContainer:hover .cardFront {
  left: -35rem;
}

.cardContainer:hover .cardBack {
  left: 0px;
}
